/* MovieBookingPage.css */

/* Global Styles */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f3f3f3;
  }
  
  .movie-booking-page {
    padding: 20px;
  }
  
  /* Movie container */
  .movie-container {
    margin-bottom: 20px;
  }
  
  .movie-container select {
    background-color: #fff;
    border: 0;
    border-radius: 5px;
    font-size: 14px;
    font-family: inherit;
    margin-left: 10px;
    padding: 5px 15px;
    appearance: none;
  }
  
  /* Seats container and styling */
  .container {
    position: relative;
    margin: 20px auto;
    display: grid;
    gap: 10px;
  }
  
  .rows {
    display: grid;
    grid-template-columns: repeat(8, 40px);
    gap: 10px;
  }
  
  .screen {
    width: 80%;
    height: 50px;
    background-color: #444;
    margin: 20px auto;
  }
  
  /* Seat styling */
  .seat {
    width: 40px;
    height: 40px;
    background-color: #444;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .seat.selected {
    background-color: #6feaf6;
  }
  
  .seat.occupied {
    background-color: #ff6347;
    cursor: not-allowed;
  }
  
  /* Showcase for seat legend */
  ul.showcase {
    display: flex;
    gap: 15px;
    justify-content: center;
    list-style: none;
    padding: 0;
  }
  
  ul.showcase .seat {
    cursor: default;
  }
  
  /* Text and button styling */
  .text {
    text-align: center;
    font-size: 18px;
  }
  